#MobileNav{
    position: fixed;
    margin-left: 10px;
    margin-top: 10px;
    z-index: 1000;
    transition:  2s ease-in-out;
    cursor: pointer;
}
#MobileNav ul{
    list-style: none;
    text-align:center;
    margin: auto;
}
#MobileNav ul li{
    border-bottom: 1px solid #fff;
    padding: 12px;
    text-align: center;
}
#MobileNav a{
    text-decoration: none;
    color:#fff;
    font-size: 1.8rem;
    font-family: "Montserrat";
    text-align: center;
}
#MobileNav a:hover{
    color:#ffdf05;
}