#Home{
    background-color: black;
    height: fit-content;
    width: 100%;
}
img{
    width:80%;
    height: auto;
    text-align: center;
}
.title-img{
    opacity: 1;
    animation: title-animation 10s infinite;
}

@keyframes title-animation{
from{ 
    opacity: 0;
    transform: scale(0)
}
to{
    opacity: 1;
    transform: scale(100%)
}
}